.conteudo {

    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
}
.background {

    height: 100vh;
    position: absolute;
    left: 0;
}

.info {

    width: 60%;
    margin-right: 90px;
    margin-top: 40px;
    color: #FFFFFF;

}

input {

    border: 1px solid  #999999;
    border-radius: 22px;
    width: 500px;
    height: 50px;
    margin-right: 12px;
    background: transparent;
    font-size: 18px;
    padding: 5px;
    color:#ffffff;

}

button {
  

        border: 1px solid  #999999;
        border-radius: 22px;
        margin-right: 12px;
        background: #20333B;
        font-size: 18px;
        padding: 10px;
        color:#FFFFFF;
        height: 55px;
        width: 150px;
}

.perfil {

    display: flex;
    margin: 40px 0;
    
}

.profile {

    width: 120px;
    height: 120px;
    border-radius: 60px;
    border: 2px solid #E5E5;
    margin-right: 32px;

}

.perfil h3 {

    color: #999999;
    font-size: 24px;
    margin: 0;
}


.perfil p {

    color: #999999;
    font-size: 12px;
    margin: 20px 0 0 0;

}

.perfil span {
    display: block;
    color: #999999;
    font-size: 12px;
    margin: 0;

}
hr {

    color: #999999;
   
}

.repositorio {

    font-size: 32px;
    color: #ffffff;
    margin: 12px 0;
    text-align: center;

}